import {
	mapState,
	mapMutations,
	mapActions

} from 'vuex';
export default {
	data() {
		return {
			isShow:false,
		};
	},
	computed: mapState(['serverUrl']),
	onLoad() { //默认加载
	console.log("dologin")
		this.doLogin();
	},
	methods: {
		...mapMutations(['login']),
		...mapActions(['updateUserInfo']),
		//第一授权获取用户信息===》按钮触发
		wxGetUserInfo() {
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					console.log(infoRes.userInfo)
					_this.login(infoRes.userInfo)
					uni.reLaunch({
						url:'/pages/tabs/index/index'
					})
					try {
						_this.updateUserInfo();
					} catch (e) {}
				},
				fail(res) {}
			});
		},
		doLogin() {
			var _this =this;
			uni.showLoading({
				title: '加载中...'
			});
			uni.request({
				url: _this.serverUrl +'/wx/check/code', //仅为示例，并非真实接口地址。
				// methods:"POST",
				data: {
					code: uni.getStorageSync('token')
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (sres) => {
					console.log("获取用户信息", sres);

					if (sres.data.code == 403) {
						// 1.wx获取登录用户code
						uni.login({
							provider: 'weixin',
							success: function(loginRes) {
								let code = loginRes.code;
								console.log("返回code", code)
								uni.request({
									url: _this.serverUrl + '/wx/login',
									data: {
										code: code,
									},
									method: 'POST',
									header: {
										'content-type': 'application/x-www-form-urlencoded',
									},
									success: (res) => {
										var cookie = res.header['Set-Cookie']
										
										uni.setStorage({ //缓存用户登陆状态
											key: 'token',
											data: res.data.data.token
										})
										uni.setStorage({ //缓存用户登陆状态
											key: 'setcookie',
											data: cookie
										})
										console.log("loginres",res)
										if(res.data.data.userStatus== 0){
											_this.isShow = true;
										}else {
											uni.reLaunch({
												url:'/pages/tabs/index/index'
											})
										}
										uni.hideLoading();
									}
								});
							},
						});
						return;
					}else{
						uni.reLaunch({
							url:'../tabs/index/index'
						})
					}
				}
			});
		},





		//向后台更新信息
		// updateUserInfo() {
		// 	let _this = this;
		// 	uni.request({
		// 		url: 'url', //服务器端地址
		// 		data: {
		// 			customerId: _this.customerId,
		// 			nickName: _this.nickName,
		// 			headUrl: _this.avatarUrl
		// 		},
		// 		method: 'POST',
