		// 			'content-type': 'application/json'
		// 		},
		// 		success: (res) => {
		// 			if (res.data.state == "success") {
		// 				uni.reLaunch({ //信息更新成功后跳转到小程序首页
		// 					url: '/pages/index/index'
		// 				});
		// 			}
		// 		}

		// 	});
		// }
	},

}
